@import './helper.scss';

.home {
  @include col;
  width: 100%;
  height: 100vh;

  &-image {
    width: 90vw;
    margin-top: 20px;
    height: calc(100vh - $header-height - 40px);

    border-radius: 20px;
    display: block;

    object-fit: cover;
    transition: scale 0.6s ease-in-out;
  }

  &-text {
    font: {
      size: 50px;
      weight: 400;
      family: $arvo;
    }

    color: white;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -75%);
    @include mobile {
      transform: translate(-50%, -50%);
    }
    z-index: 5;
    white-space: pre-line;
    opacity: 0;

    transition: opacity 2s ease-in-out;
  }

  &-scrolled {
    scale: 0.8;
  }
}
