@tailwind base;
@tailwind components;
@tailwind utilities;
@import './components/helper.scss';

.App {
  text-align: center;
  padding-top: 64px;
  font-family: $sans;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

.page-title {
  color: white;
  font-size: 50px;
  width: 100%;
  text-align: center;
  font-weight: 900;
  font-family: $josefin;

  @media screen and (max-width: 480px) {
    font-size: 30px;
  }
}

.page-padding {
  padding-top: 150px;
  padding-bottom: 150px;

  @media screen and (max-width: 480px) {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
